/* eslint-disable react-hooks/exhaustive-deps */
// @mui
import { Container, Grid, Typography, Card, Stack, alpha, Box } from '@mui/material';
import { LoadingButton } from '@mui/lab';
import { useState, useEffect } from 'react';
import { useSnackbar } from 'notistack';
// hooks
import useSettings from '../../hooks/useSettings';
// components
import Page from '../../components/Page';
import HeaderBreadcrumbs from '../../components/HeaderBreadcrumbs';
import axios from '../../utils/axios';

// sections
import {
  NumberForm,
  AppWidgetSummary,
} from '../../sections/@dashboard/general/app';
import AdminNetworkBalance from '../../sections/@dashboard/general/app/AdminNetworkBalance';
import UserChat from '../../sections/@dashboard/admin/userchat';

// ----------------------------------------------------------------------

export default function AdminGeneralApp() {
  const { themeStretch } = useSettings();
  const [myusers, RealUser] = useState([]);
  const [load, SetLoad] = useState(true);
  const [lazy, SetLazy] = useState(false);
  const { enqueueSnackbar } = useSnackbar();
  const AccessToken = window.localStorage.getItem('accessToken');

  useEffect(() => {
    initialize();
  }, []);

  const initialize = async () => {
    SetLoad(true);
    await axios
      .get(`/api/system/all/user/records/admin/safe/url/${AccessToken}/secure`)
      .then((data) => {
        RealUser(data.data.user);
        SetLoad(false);
      })
      .catch(() => SetLoad(false));
  };

  const MakeSuccess = async () => {
    SetLazy(true);
    try {
      await axios.get('/api/success/system/success');
      SetLazy(false);
      initialize();
      enqueueSnackbar('System synchronized successfully');
    } catch (_) {
      SetLazy(false);
      enqueueSnackbar('Failed to synchronize', { variant: 'error' });
    }
  };

  const MakeFail = async () => {
    SetLazy(true);
    try {
      await axios.get('/api/refund/system/refund');
      initialize();
      SetLazy(false);
      enqueueSnackbar('Emergency refund processed');
    } catch (_) {
      SetLazy(false);
      enqueueSnackbar('Refund operation failed', { variant: 'error' });
    }
  };

  // Prepare network balance data
  const networkBalances = [
    { id: 1, name: 'MTN CG', balance: myusers?.mtn_cg_bal, image: '/icons/mtn.png' },
    { id: 2, name: 'MTN GIFTING', balance: myusers?.mtn_g_bal, image: '/icons/mtn.png' },
    { id: 3, name: 'MTN SME', balance: myusers?.mtn_sme_bal, image: '/icons/mtn.png' },
    { id: 4, name: 'AIRTEL CG', balance: myusers?.airtel_cg_bal, image: '/icons/airtel.png' },
    { id: 5, name: '9MOBILE SME', balance: myusers?.mobile_sme_bal, image: '/icons/9mobile.png' },
    { id: 6, name: 'GLO SME', balance: myusers?.glo_sme_bal, image: '/icons/glo.png' },
  ];

  return (
    <Page title="Admin Dashboard">
      <Container maxWidth={themeStretch ? false : 'xl'}>
        <HeaderBreadcrumbs
          heading={'Admin Dashboard Developed By HABUKHAN developers'}
          links={[
            { name: 'support' },
            { name: '07040540018' },
          ]}
        />

        <Grid container spacing={3} sx={{ mt: 2 }}>
          {!load ? (
            <>
              {/* Main KPI Row - Moved to Top */}
              <Grid item xs={12} sm={4} md={3}>
                <AppWidgetSummary
                  title="Total Wallet Assets"
                  total={myusers?.wallet_balance || 0}
                  chartColor="#00AB55"
                  percent={2.6}
                  chartData={[5, 18, 12, 51, 68, 11, 39, 37, 27, 20]}
                />
              </Grid>

              <Grid item xs={12} sm={4} md={3}>
                <AppWidgetSummary
                  title="Unpaid Commissions"
                  total={myusers?.ref_balance || 0}
                  chartColor="#FFC107"
                  percent={-0.1}
                  chartData={[20, 41, 63, 33, 28, 35, 50, 46, 11, 26]}
                />
              </Grid>

              <Grid item xs={12} sm={4} md={3}>
                <AppWidgetSummary
                  title="Registered Base"
                  total={myusers?.all_user || 0}
                  chartColor="#1890FF"
                  percent={12.4}
                  chartData={[8, 9, 31, 8, 27, 37, 56, 23, 24, 77]}
                />
              </Grid>

              <Grid item xs={12} sm={4} md={3}>
                <AppWidgetSummary
                  title="Queue Traffic"
                  total={(myusers?.total_process || 0) + (myusers?.total_data_proccess || 0)}
                  chartColor="#FF4842"
                  percent={0.5}
                  chartData={[8, 23, 44, 12, 12, 12, 12, 44, 23, 8]}
                />
              </Grid>

              {/* Today's Sales Performance (Live Stats) - Moved Down */}
              <Grid item xs={12} sm={6}>
                <AppWidgetSummary
                  title="Today's Total Sales"
                  total={myusers?.today_sales || 0}
                  chartColor="#00AB55"
                  percent={myusers?.sales_percent || 0}
                  chartData={[8, 9, 31, 8, 27, 37, 56, 23, 24, 77]}
                  isCurrency={true}
                />
              </Grid>

              <Grid item xs={12} sm={6}>
                <AppWidgetSummary
                  title="Successful Transactions Today"
                  total={(myusers?.today_data_success || 0) + (myusers?.today_airtime_success || 0)}
                  chartColor="#1890FF"
                  percent={myusers?.trans_percent || 0}
                  chartData={[20, 41, 63, 33, 28, 35, 50, 46, 11, 26]}
                />
              </Grid>

              {/* Data Visualization & Provder Status */}
              <Grid item xs={12} md={6} lg={8}>
                <UserChat myusers={myusers} />
              </Grid>

              <Grid item xs={12} md={6} lg={4}>
                <AdminNetworkBalance
                  title="Internal Stock Inventory"
                  subheader="Resource pool allocation status"
                  list={networkBalances}
                />
              </Grid>

              {/* Sub Categories / User Types */}
              <Grid item xs={12} md={4}>
                <NumberForm title="Smart Members" total={myusers?.smart_total || 0} />
              </Grid>
              <Grid item xs={12} md={4}>
                <NumberForm title="Agent Partners" total={myusers?.agent_total || 0} />
              </Grid>
              <Grid item xs={12} md={4}>
                <NumberForm title="API Integrations" total={myusers?.api_total || 0} />
              </Grid>

              {/* Action Hub - Redesigned & Responsive */}
              <Grid item xs={12}>
                <Card
                  sx={{
                    p: 3,
                    textAlign: 'center',
                    background: (theme) => alpha(theme.palette.primary.main, 0.04),
                    border: (theme) => `1px dashed ${theme.palette.primary.main}`,
                  }}
                >
                  <Typography variant="h6" gutterBottom>
                    System Operations
                  </Typography>

                  {/* Pending Count Badge */}
                  <Box sx={{ mb: 2 }}>
                    <Typography
                      variant="overline"
                      sx={{
                        color: myusers?.total_pending > 0 ? 'error.main' : 'text.disabled',
                        fontWeight: 'bold',
                        bgcolor: (theme) => alpha(myusers?.total_pending > 0 ? theme.palette.error.main : theme.palette.text.disabled, 0.1),
                        px: 2,
                        py: 0.5,
                        borderRadius: 1
                      }}
                    >
                      {myusers?.total_pending || 0} Pending Transactions Across All Services
                    </Typography>
                  </Box>

                  <Typography variant="body2" sx={{ color: 'text.secondary', mb: 3 }}>
                    Actions below will apply to all pending **Data, Airtime, Cable, and Electricity** transactions.
                  </Typography>

                  <Stack
                    direction={{ xs: 'column', sm: 'row' }}
                    spacing={2}
                    justifyContent="center"
                    alignItems="center"
                  >
                    <LoadingButton
                      fullWidth={{ xs: true, sm: false }}
                      size="large"
                      variant="contained"
                      loading={lazy}
                      onClick={MakeSuccess}
                      sx={{ px: { xs: 2, md: 8 }, py: 1.5 }}
                      disabled={!myusers?.total_pending}
                    >
                      Synchronize All ({myusers?.total_pending || 0})
                    </LoadingButton>
                    <LoadingButton
                      fullWidth={{ xs: true, sm: false }}
                      size="large"
                      color="error"
                      variant="outlined"
                      loading={lazy}
                      onClick={MakeFail}
                      sx={{ px: { xs: 2, md: 8 }, py: 1.5 }}
                      disabled={!myusers?.total_pending}
                    >
                      Mass Refund ({myusers?.total_pending || 0})
                    </LoadingButton>
                  </Stack>
                </Card>
              </Grid>
            </>
          ) : (
            <Grid item xs={12} sx={{ textAlign: 'center', py: 10 }}>
              <Typography variant="h6" color="text.secondary">
                Loading Analytics...
              </Typography>
            </Grid>
          )}
        </Grid>
      </Container>
    </Page>
  );
}
