import PropTypes from 'prop-types';
// @mui
import { Card, CardHeader, Table, TableRow, TableBody, TableCell, TableContainer } from '@mui/material';
// utils
import { fCurrency } from '../../../../utils/formatNumber';
// components
import Scrollbar from '../../../../components/Scrollbar';
import Image from '../../../../components/Image';

// ----------------------------------------------------------------------

AdminNetworkBalance.propTypes = {
    title: PropTypes.string,
    subheader: PropTypes.string,
    list: PropTypes.array.isRequired,
};

export default function AdminNetworkBalance({ title, subheader, list, ...other }) {
    return (
        <Card {...other}>
            <CardHeader title={title} subheader={subheader} />
            <Scrollbar>
                <TableContainer sx={{ minWidth: 240, mt: 2 }}>
                    <Table>
                        <TableBody>
                            {list.map((item) => (
                                <TableRow key={item.id}>
                                    <TableCell sx={{ display: 'flex', alignItems: 'center' }}>
                                        <Image
                                            alt={item.name}
                                            src={item.image}
                                            sx={{ width: 40, height: 40, borderRadius: 1.5, mr: 2 }}
                                        />
                                        {item.name}
                                    </TableCell>
                                    <TableCell align="right" sx={{ fontWeight: 'bold' }}>
                                        ₦{fCurrency(item.balance)}
                                    </TableCell>
                                </TableRow>
                            ))}
                        </TableBody>
                    </Table>
                </TableContainer>
            </Scrollbar>
        </Card>
    );
}
